body {
  margin: 0;
  min-height: 100%;
  font-family: 'VT323', monospace;
 }

.main-page-container{
  background-color: rgb(34, 37, 41);
  color: white;
  border-radius: 5px;
  border: 1px gray solid;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: auto;
  max-width: 500px;
  /* min-width: 200px; */
  min-height: 200px;
  /* margin-left: 10px;
  margin-right: 10px; */
  padding-left: 10px;
  padding-right: 10px;
}

.card{
  border: 0px;
  background-color: rgb(34, 37, 41);
  color: white!important;
}

html { 
  background: rgb(29, 29, 29); 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  padding: 0;
  margin: 0;
}

.btn-primary {
  background-color: rgb(213, 46, 62);
}

.bg-danger {
  background: rgb(29, 29, 29)!important;
}

.bg-success {
  background: rgb(29, 29, 29)!important;
}

.bg-warning {
  background: rgb(29, 29, 29)!important;
}

.default-button{
  color: rgb(95, 198, 154);
  border: 2px rgb(95, 198, 154) solid!important;
  border-radius: 5px;
  background-color: rgb(29, 29, 29);
}

.default-button:hover{
  background-color: rgb(12, 194, 115);
}

.default-button-danger{
  color: rgb(213, 46, 62);
  border: 2px rgb(213, 46, 62) solid!important;
  border-radius: 5px;
  background-color: rgb(29, 29, 29);
}

.default-button-danger:hover{
  background-color: rgb(213, 46, 62);
}